* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    background: rgb(243, 208, 208);
    color: black;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

@mixin verticalAlign {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}